  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 100%;
  font-weight: 300;
  letter-spacing: 0.27em;
  color: #919191;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  margin: 0;
  margin-bottom: 4px;
}

.user-account {
  display: flex;
}

.user-account__item:not(:last-child) {
  border-right: 1px solid #303030;
}

.user-account__link {
  display: block;
  width: fit-content;
  padding: 0 10px;
}

.account-nav {
  max-width: 216px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 28px;
}

.account-nav__item {
  position: relative;
  width: 20px;
  height: 20px;
}

.account-nav__link {
  display: block;
}

.account-nav__link-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 2px;
}